/****************************************************
loading animation
 */
.load {
  position: relative;
  width: 234px;
  height: 28px;
  margin: auto; }
  .load .load-circle {
    position: absolute;
    top: 0;
    background-color: #0082fe;
    width: 28px;
    height: 28px;
    animation-name: bounce_loading;
    -o-animation-name: bounce_loading;
    -ms-animation-name: bounce_loading;
    -webkit-animation-name: bounce_loading;
    -moz-animation-name: bounce_loading;
    animation-duration: 1.2s;
    -o-animation-duration: 1.2s;
    -ms-animation-duration: 1.2s;
    -webkit-animation-duration: 1.2s;
    -moz-animation-duration: 1.2s;
    animation-iteration-count: infinite;
    -o-animation-iteration-count: infinite;
    -ms-animation-iteration-count: infinite;
    -webkit-animation-iteration-count: infinite;
    -moz-animation-iteration-count: infinite;
    animation-direction: normal;
    -o-animation-direction: normal;
    -ms-animation-direction: normal;
    -webkit-animation-direction: normal;
    -moz-animation-direction: normal;
    transform: scale(0);
    -o-transform: scale(0);
    -ms-transform: scale(0);
    -webkit-transform: scale(0);
    -moz-transform: scale(0);
    border-radius: 19px;
    -o-border-radius: 19px;
    -ms-border-radius: 19px;
    -webkit-border-radius: 19px;
    -moz-border-radius: 19px; }
  .load .load-circle_item_1 {
    left: 0;
    animation-delay: 0.15s;
    -o-animation-delay: 0.15s;
    -ms-animation-delay: 0.15s;
    -webkit-animation-delay: 0.15s;
    -moz-animation-delay: 0.15s; }
  .load .load-circle_item_2 {
    left: 29px;
    animation-delay: 0.3s;
    -o-animation-delay: 0.3s;
    -ms-animation-delay: 0.3s;
    -webkit-animation-delay: 0.3s;
    -moz-animation-delay: 0.3s; }
  .load .load-circle_item_3 {
    left: 58px;
    animation-delay: 0.45s;
    -o-animation-delay: 0.45s;
    -ms-animation-delay: 0.45s;
    -webkit-animation-delay: 0.45s;
    -moz-animation-delay: 0.45s; }
  .load .load-circle_item_4 {
    left: 88px;
    animation-delay: 0.6s;
    -o-animation-delay: 0.6s;
    -ms-animation-delay: 0.6s;
    -webkit-animation-delay: 0.6s;
