@-webkit-keyframes bounce_loading {
  0% {
    -webkit-transform: scale(0);
            transform: scale(0);
    background-color: #8884d8; }
  50% {
    -webkit-transform: scale(1);
            transform: scale(1);
    background-color: #0082fe; }
  100% {
    -webkit-transform: scale(0);
            transform: scale(0);
    background-color: #8884d8;
    opacity: 0; } }

@keyframes bounce_loading {
  0% {
    -webkit-transform: scale(0);
            transform: scale(0);
    background-color: #8884d8; }
  50% {
    -webkit-transform: scale(1);
            transform: scale(1);
    background-color: #0082fe; }
  100% {
    -webkit-transform: scale(0);
            transform: scale(0);
    background-color: #8884d8;
    opacity: 0; } }

