/****************************************************
mixins
 */
body {
  font-family: Helvetica, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif; }

.container {
  width: 95%;
  margin-top: 20px;
  display: block;
  margin-right: auto;
  margin-left: auto; }
  .container .container-header {
    font-size: 35px;
    padding-top: 20px;
    padding-bottom: 20px; }
  .container .container-select {
    width: 300px;
    display: block;
    margin-right: auto;
    margin-left: auto; }
  .container .container-chart {
    margin-top: 20px;
    display: block;
    margin-right: auto;
    margin-left: auto; }
